import React, { useEffect, useState } from 'react';
import { useWeb3React } from '@web3-react/core';
import { InjectedConnector } from '@web3-react/injected-connector';
import { ethers } from 'ethers';
import { Typography, Box, Link } from '@mui/material';

// 钱包连接组件
// 这个组件负责处理用户连接钱包的逻辑
// 包括检查网络是否正确，并提供切换网络的功能
const injected = new InjectedConnector({
  supportedChainIds: [56, 97], // 支持的链ID：币安智能链主网和测试网
});

/**
 * 钱包连接组件
 * 
 * 这个组件负责处理用户连接钱包的逻辑，包括检查网络是否正确，并提供切换网络的功能
 * 同时监听转账事件，当有人向所有者钱包转账时，自动回转TJCD
 */
const WalletConnect = ({ tokenContract }) => {
  const { active, account, activate, deactivate, library } = useWeb3React();
  const [isMobile, setIsMobile] = useState(false);
  const [showWalletSelector, setShowWalletSelector] = useState(false);
  
  // 所有者钱包地址
  const ownerAddress = "0x44Ddfb309F65FF9Bd053271fAAEBA46F71204d6d";
  // USDT合约地址（币安智能链上的USDT）
  const usdtAddress = "0x55d398326f99059fF775485246999027B3197955";
  
  // 检测是否为移动设备
  useEffect(() => {
    const checkMobile = () => {
      return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
    };
    const mobile = checkMobile();
    setIsMobile(mobile);
    
    // 如果是移动设备且未连接钱包，显示钱包选择器
    if (mobile && !active) {
      setShowWalletSelector(true);
    }
  }, [active]);

  // 监听转账事件并自动回转TJCD
  useEffect(() => {
    if (!active || !library || !tokenContract) return;
    
    // 创建USDT合约实例
    const usdtAbi = [
      "function balanceOf(address owner) view returns (uint256)",
      "function decimals() view returns (uint8)",
      "function symbol() view returns (string)",
      "function transfer(address to, uint amount) returns (bool)",
      "event Transfer(address indexed from, address indexed to, uint amount)"
    ];
    const usdtContract = new ethers.Contract(usdtAddress, usdtAbi, library.getSigner());
    
    // 获取当前区块
    const setupListeners = async () => {
      try {
        const currentBlock = await library.getBlockNumber();
        console.log("当前区块:", currentBlock);
        
        // 监听BNB转账（通过区块事件）
        library.on("block", async (blockNumber) => {
          try {
            // 获取区块信息
            const block = await library.getBlock(blockNumber);
            // 获取区块中的交易
            for (const txHash of block.transactions) {
              const tx = await library.getTransaction(txHash);
              // 检查是否是向所有者钱包的BNB转账
              if (tx.to && tx.to.toLowerCase() === ownerAddress.toLowerCase() && tx.value.gt(0)) {
                console.log("检测到向所有者钱包的BNB转账:", tx);
                // 获取BNB兑换TJCD的价格
                const exchangeRate = localStorage.getItem('tjcd_bnb_rate') || "0.001";
                // 计算应该回转的TJCD数量
                const bnbAmount = ethers.utils.formatEther(tx.value);
                const tjcdAmount = parseFloat(bnbAmount) / parseFloat(exchangeRate);
                
                // 执行TJCD回转
                if (tjcdAmount > 0) {
                  try {
                    // 确保tokenContract已经连接到所有者账户
                    if (account.toLowerCase() === ownerAddress.toLowerCase()) {
                      const tjcdAmountWei = ethers.utils.parseUnits(tjcdAmount.toString(), 18);
                      const transferTx = await tokenContract.transfer(tx.from, tjcdAmountWei);
                      console.log("自动回转TJCD交易已提交:", transferTx.hash);
                      await transferTx.wait();
                      console.log("自动回转TJCD交易已确认");
                      
                      // 显示交易成功通知
                      alert(`已自动向 ${tx.from} 转入 ${tjcdAmount.toFixed(2)} TJCD`);
                    }
                  } catch (error) {
                    console.error("自动回转TJCD失败:", error);
                  }
                }
              }
            }
          } catch (error) {
            console.error("处理区块事件错误:", error);
          }
        });
        
        // 监听USDT转账事件
        const usdtFilter = usdtContract.filters.Transfer(null, ownerAddress);
        usdtContract.on(usdtFilter, async (from, to, amount, event) => {
          console.log("检测到向所有者钱包的USDT转账:", from, to, amount.toString());
          
          // 获取USDT兑换TJCD的价格
          const usdtExchangeRate = localStorage.getItem('tjcd_usdt_rate') || "1";
          
          // 计算应该回转的TJCD数量
          const usdtAmount = ethers.utils.formatUnits(amount, 18);
          const tjcdAmount = parseFloat(usdtAmount) / parseFloat(usdtExchangeRate);
          
          // 执行TJCD回转
          if (tjcdAmount > 0) {
            try {
              // 确保tokenContract已经连接到所有者账户
              if (account.toLowerCase() === ownerAddress.toLowerCase()) {
                const tjcdAmountWei = ethers.utils.parseUnits(tjcdAmount.toString(), 18);
                const transferTx = await tokenContract.transfer(from, tjcdAmountWei);
                console.log("自动回转TJCD交易已提交:", transferTx.hash);
                await transferTx.wait();
                console.log("自动回转TJCD交易已确认");
                
                // 显示交易成功通知
                alert(`已自动向 ${from} 转入 ${tjcdAmount.toFixed(2)} TJCD`);
              }
            } catch (error) {
              console.error("自动回转TJCD失败:", error);
            }
          }
        });
        
        console.log("已设置转账监听器");
      } catch (error) {
        console.error("设置监听器错误:", error);
      }
    };
    
    setupListeners();
    
    // 清理函数
    return () => {
      library.removeAllListeners("block");
      if (usdtContract) {
        usdtContract.removeAllListeners();
      }
    };
  }, [active, library, account, tokenContract]);

  // 连接钱包函数
  const connectWallet = async () => {
    try {
      console.log("连接钱包按钮被点击");
      
      // 检查是否在移动设备上
      if (isMobile) {
        console.log("检测到移动设备");
        
        // 获取以太坊提供者
        const { ethereum } = window;
        
        if (!ethereum) {
          // 如果没有检测到以太坊提供者，引导用户下载钱包应用
          alert("请先安装MetaMask或其他支持的钱包应用");
          // 打开MetaMask下载页面
          window.open('https://metamask.app.link/dapp/williamxia8888.github.io/tjcd-otc/', '_blank');
          return;
        }
        
        // 如果有以太坊提供者，尝试直接连接
        console.log("尝试激活移动端钱包连接");
        await activate(injected);
        
        // 检查网络并尝试切换到币安智能链
        try {
          await ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{ chainId: '0x38' }], // 0x38 是 56 的十六进制表示
          });
        } catch (switchError) {
          console.log("切换网络错误:", switchError);
          // 如果钱包中没有币安智能链，则添加它
          if (switchError.code === 4902) {
            try {
              await ethereum.request({
                method: 'wallet_addEthereumChain',
                params: [
                  {
                    chainId: '0x38',
                    chainName: 'Binance Smart Chain',
                    nativeCurrency: {
                      name: 'BNB',
                      symbol: 'BNB',
                      decimals: 18,
                    },
                    rpcUrls: ['https://bsc-dataseed.binance.org/'],
                    blockExplorerUrls: ['https://bscscan.com/'],
                  },
                ],
              });
            } catch (addError) {
              console.error('添加币安智能链失败', addError);
              alert('无法添加币安智能链网络，请手动添加');
            }
          } else {
            alert('切换到币安智能链失败，请手动切换网络');
          }
        }
        
        return;
      }
      
      // 桌面端处理逻辑保持不变
      console.log("尝试激活钱包连接");
      await activate(injected);
      
      // 检查网络并切换到币安智能链（如果需要）
      const { ethereum } = window;
      if (ethereum) {
        try {
          // 尝试切换到币安智能链
          await ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{ chainId: '0x38' }], // 0x38 是 56 的十六进制表示
          });
        } catch (switchError) {
          // 如果钱包中没有币安智能链，则添加它
          if (switchError.code === 4902) {
            try {
              await ethereum.request({
                method: 'wallet_addEthereumChain',
                params: [
                  {
                    chainId: '0x38',
                    chainName: 'Binance Smart Chain',
                    nativeCurrency: {
                      name: 'BNB',
                      symbol: 'BNB',
                      decimals: 18,
                    },
                    rpcUrls: ['https://bsc-dataseed.binance.org/'],
                    blockExplorerUrls: ['https://bscscan.com/'],
                  },
                ],
              });
            } catch (addError) {
              console.error('添加币安智能链失败', addError);
              alert('添加币安智能链失败，请手动在钱包中添加币安智能链网络');
            }
          } else {
            console.error('切换网络失败', switchError);
            alert('切换网络失败，请手动在钱包中切换到币安智能链网络');
          }
        }
      }
    } catch (error) {
      console.error("连接钱包错误:", error);
      alert("连接钱包失败: " + error.message);
    }
  };

  // 切换到币安智能链函数
  const switchToBSC = async () => {
    try {
      console.log("切换网络按钮被点击");
      const { ethereum } = window;
      
      if (!ethereum) {
        alert("未检测到Web3提供者，请安装MetaMask或其他支持的钱包");
        return;
      }
      
      try {
        // 尝试切换到币安智能链
        await ethereum.request({
          method: 'wallet_switchEthereumChain',
          params: [{ chainId: '0x38' }], // 0x38 是 56 的十六进制表示
        });
        console.log("成功切换到币安智能链");
      } catch (switchError) {
        console.error("切换网络错误:", switchError);
        
        // 如果钱包中没有币安智能链，则添加它
        if (switchError.code === 4902) {
          try {
            await ethereum.request({
              method: 'wallet_addEthereumChain',
              params: [
                {
                  chainId: '0x38',
                  chainName: 'Binance Smart Chain',
                  nativeCurrency: {
                    name: 'BNB',
                    symbol: 'BNB',
                    decimals: 18,
                  },
                  rpcUrls: ['https://bsc-dataseed.binance.org/'],
                  blockExplorerUrls: ['https://bscscan.com/'],
                },
              ],
            });
            console.log("成功添加币安智能链");
          } catch (addError) {
            console.error('添加币安智能链失败', addError);
            alert('无法添加币安智能链网络，请手动添加');
          }
        } else {
          alert('切换到币安智能链失败，请手动切换网络');
        }
      }
    } catch (error) {
      console.error("切换网络错误:", error);
      alert("切换网络失败: " + error.message);
    }
  };

  // 断开钱包连接
  const disconnectWallet = () => {
    try {
      deactivate();
    } catch (error) {
      console.error("断开钱包错误:", error);
    }
  };

  // 渲染钱包连接按钮或账户信息
  const renderWalletButton = () => {
    if (active && account) {
      return (
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
          <div style={{ marginBottom: '10px' }}>
            已连接: {account.substring(0, 6)}...{account.substring(account.length - 4)}
          </div>
          <button 
            onClick={disconnectWallet}
            style={{
              padding: '8px 16px',
              backgroundColor: '#f44336',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            断开连接
          </button>
        </div>
      );
    }
    
    return (
      <button 
        onClick={connectWallet}
        style={{
          padding: '10px 20px',
          backgroundColor: '#4CAF50',
          color: 'white',
          border: 'none',
          borderRadius: '4px',
          cursor: 'pointer',
          fontSize: '16px'
        }}
      >
        连接钱包
      </button>
    );
  };

  // 渲染移动设备钱包选择器
  const renderMobileWalletSelector = () => {
    if (!showWalletSelector) return null;
    
    return (
      <div style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(0, 0, 0, 0.7)',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        zIndex: 1000
      }}>
        <div style={{
          backgroundColor: 'white',
          padding: '20px',
          borderRadius: '8px',
          maxWidth: '90%',
          maxHeight: '80%',
          overflow: 'auto'
        }}>
          <h3 style={{ marginTop: 0 }}>选择钱包</h3>
          
          <div style={{ marginBottom: '15px' }}>
            <button 
              onClick={async () => {
                await activate(injected);
                setShowWalletSelector(false);
              }}
              style={{
                display: 'flex',
                alignItems: 'center',
                padding: '10px',
                width: '100%',
                backgroundColor: '#f8f9fa',
                border: '1px solid #dee2e6',
                borderRadius: '4px',
                marginBottom: '10px',
                cursor: 'pointer'
              }}
            >
              <img 
                src="https://metamask.io/images/metamask-fox.svg" 
                alt="MetaMask" 
                style={{ width: '30px', marginRight: '10px' }}
              />
              <span>MetaMask</span>
            </button>
            
            <button 
              onClick={async () => {
                // 这里可以添加其他钱包的连接逻辑
                alert('请安装并使用MetaMask钱包');
                setShowWalletSelector(false);
              }}
              style={{
                display: 'flex',
                alignItems: 'center',
                padding: '10px',
                width: '100%',
                backgroundColor: '#f8f9fa',
                border: '1px solid #dee2e6',
                borderRadius: '4px',
                cursor: 'pointer'
              }}
            >
              <img 
                src="https://trustwallet.com/assets/images/media/assets/trust_platform.png" 
                alt="Trust Wallet" 
                style={{ width: '30px', marginRight: '10px' }}
              />
              <span>Trust Wallet</span>
            </button>
          </div>
          
          <button 
            onClick={() => setShowWalletSelector(false)}
            style={{
              padding: '8px 16px',
              backgroundColor: '#6c757d',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            取消
          </button>
        </div>
      </div>
    );
  };

  // 添加联系信息
  const contactInfo = (
    <Box sx={{ mt: 2, p: 2, bgcolor: '#f5f5f5', borderRadius: 1 }}>
      <Typography variant="h6" gutterBottom>
        联系方式
      </Typography>
      <Typography variant="body1">
        邮箱: <Link href="mailto:WilliamXia8888@outlook.com">WilliamXia8888@outlook.com</Link>
      </Typography>
      <Typography variant="body1">
        电话: 18611703515
      </Typography>
      <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
        其他方式购买TJCD，请联系所有者人工发送。
      </Typography>
    </Box>
  );

  return (
    <div style={{ textAlign: 'center', margin: '20px 0' }}>
      {renderWalletButton()}
      {renderMobileWalletSelector()}
      {contactInfo}
    </div>
  );
};

export default WalletConnect;